.movies {
	box-sizing: content-box;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-wrap: wrap;
	padding: 50px;
	padding-top:70px;
	width: 1300px;
}

.movies .movie:hover {
    transform: translateY(-7px);
}

.movies .movie {
	width: 45%;
	background-color: white;
	margin-bottom: 70px;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	font-weight: 300;
	padding: 20px;
	border-radius: 5px;
	color: #adaeb9;
	cursor: pointer;
	transition: transform .3s ease-in-out;
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
	
}

.movie img {
	max-width: 185px;
	margin-right: 30px;
	box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}


.movie .movie__title,
.movie .movie__genres {
	margin: 0;
	font-weight: 300;
}

.movie .movie__title {
	margin-bottom: 5px;
	font-size: 24px;
	color: #2c2c2c;
}

.movie .movie__genres {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	margin: 5px 0px;
	justify-content:flex-start;
}

.movie__genres li {
	margin-right: 10px;
	font-size: 14px;
}
